@import "mixin";
$midnight: #3a3a3a20;
$clouds: #ecf0f1;
// General
body {
  color: $midnight;
  background: $clouds;
  padding: 0 1em 1em;
  background-image: url("./bg2.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  min-height: 100vh;
  background-position: center;
}
h1 {
  margin: 0;
  line-height: 2;
  text-align: center;
  color: $clouds;
}
h2 {
  margin: 0 0 0.5em;
  font-weight: normal;
  font-size: 8vh;
  text-align: center;
  color: $clouds;
}
h4 {
  font-weight: bold;
  font-size: 2.4vh;
  text-align: center;
  color: rgb(5, 1, 1);
}
input {
  position: absolute;
  opacity: 0;
  z-index: -1;
}
// Layout
.row {
  display: flex;
  .col {
    flex: 1;
    // &:last-child {
    //   margin-left: 1em;
    // }
  }
}
/* Accordion styles */
.tabs {
  border-radius: 8px;
  overflow: hidden;
  box-shadow: 0 4px 4px -2px rgba(0, 0, 0, 0.5);
}
.tab {
  width: 100%;
  color: white;
  min-height: 70px;
  &-label {
    display: flex;
    justify-content: space-between;
    padding: 1em;
    background: $midnight;
    font-weight: bold;
    cursor: pointer;
    /* Icon */
    &:hover {
      background: darken($midnight, 10%);
    }
    &::after {
      content: "\276F";
      width: 1em;
      height: 1em;
      text-align: center;
      transition: all 0.35s;
    }
  }
  &-contentSoon {
    width: 100%;
    height: auto;
    margin: 0;
    max-height: 0;
  }
  &-content {
    max-height: 0;
    padding: 0 1em;
    color: $midnight;
    transition: all 0.35s;
    height: 65vh;
    position: relative;
    perspective: 1000px;
    transform-style: preserve-3d;
    .item {
      transition: all 0.4s ease;
      position: absolute;
      width: auto;
      height: calc(100% - 20px);
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      display: flex;
      flex-direction: column;
      padding: 10px 0;
      margin: auto;
      @include md {
        max-width: 80%;
        object-fit: contain;
      }
      &.prev {
        transform: translate3d(-65%, 0, -300px);
      }

      &.next {
        transform: translate3d(65%, 0, -300px);
      }

      &.active {
        transform: translate3d(0, 0, 0);
      }
      &.inactive {
        transform: translate3d(0, -2px, -500px);
      }
    }
  }
  &-close {
    display: flex;
    justify-content: flex-end;
    padding: 1em;
    font-size: 0.75em;
    background: $midnight;
    cursor: pointer;
    &:hover {
      background: darken($midnight, 10%);
    }
  }
}

// :checked
input:checked {
  + .tab-label {
    background: darken($midnight, 10%);
    &::after {
      transform: rotate(90deg);
    }
  }
  ~ .tab-content {
    max-height: 100vh;
    padding: 1em;
  }
  ~ .tab-contentSoon {
    max-height: 100vh;
  }
}
::-webkit-scrollbar {
  width: 0;
  height: 0;
}
